*{
  margin: 0;
  padding: 0;
  box-sizing:border-box;
}

body {
  margin: 0;
  padding: 0;
  background: radial-gradient(circle, rgb(5, 0, 36), rgba(5, 0, 36, 0.911) 70.71%) center center fixed;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.outlet {
  color: rgb(18, 211, 1);
}

/* NAVBAR CSS */

.nav-bar {
  display: flex;
  position: sticky;
  align-items: center;
  align-content: center;
  justify-content: space-between;
  padding: .5rem clamp(1rem, 3.5vw, 5rem);
  background-color: rgba(255, 255, 255, .15);
  backdrop-filter: blur(5px);
}

.nav-bar * {
  display: inline;
  font-size: clamp(1rem, 3.5vw, 2rem);
  color: rgb(18, 211, 1);
  background-color: rgba(0, 0, 0, 0);
}

.nav-bar button {
  display: flex;
  align-items: center;
  gap: .5rem;
}

.nav-bar p {
  margin-bottom: 0;
}

.nav-wrap {
  display: flex;
  gap: clamp(1px, 2.5vw, 4rem);
}

.glitch {
  position: relative;
  animation: glitchy 1.6s alternate-reverse infinite;
}

@keyframes glitchy {

  2%,
  64% {
    transform: translate(0px, 0) skew(0deg);
  }

  4%,
  60% {
    transform: translate(0px, 0) skew(0deg);
  }

  62% {
    transform: translate(0, 0) skew(0deg);
  }
}

.glitch:before {
  content: attr(effect);
  position: absolute;
  width: 100%;
  animation: glitchTop 1s linear infinite;
  clip-path: polygon(0% 0, 100% 0, 100% 33%, 0 33%);
  -webkit-clip-path: polygon(67% 0, 100% 0, 100% 53%, 0 53%);
  }

  @keyframes glitchTop {

    2%,
    64% {
      transform: translate(1px, 0px);
    }

    4%,
    60% {
      transform: translate(-1px, 0px);
    }

    62% {
      transform: translate(22px, -2px) skew(-21deg);
    }
  }


.glitch:after {
  content: attr(effect);
  position: absolute;
  left: 2px;
  top: 0;
  width: 100%;
  animation: glitchBotom 1.5s linear infinite;
  clip-path: polygon(0 67%, 100% 67%, 100% 100%, 0 100%);
  -webkit-clip-path: polygon(0 67%, 100% 67%, 100% 100%, 0 100%);
  }

  @keyframes glitchBotom {

    2%,
    64% {
      transform: translate(-2px, 0);
    }

    4%,
    60% {
      transform: translate(-2px, 0);
    }

    62% {
      transform: translate(-22px, 5px) skew(21deg);
    }
}

/* STATEMENT CSS */

.top-section{
  display: flex;
  position: relative;
  flex-wrap: wrap;
  max-height: 90vh;
  margin: 3rem;
  justify-content: space-evenly;
  align-items: center;
}

.construction {
  /* display: flex; */
  color: rgb(18, 211, 1);
  margin: auto;
  text-align: center;
  width: max-content;
  max-width: 100vw;
  margin-top: 2rem;
  padding: 1.5rem;
  border-radius: 1rem;
  box-shadow: 0px 0px 15px 2px rgba(255, 255, 255, 0.849);
  background-color: rgba(255, 255, 255, 0.192);
  backdrop-filter: blur(8px);
}

.statement {
  color: rgb(18, 211, 1);
  text-align: start;
  padding: 1.5rem;
  border-radius: 1rem;
  box-shadow: 0px 0px 15px 2px rgba(0, 255, 55, 0.603);
  background-color: rgba(255, 255, 255, 0.178);
  backdrop-filter: blur(8px);
  width: 30vw;
}

.text-load {
  color: rgb(18, 211, 1);
  display: inline;
  background-color: rgba(0, 255, 55, 0.267);
}

/* IMAGE CSS */

.image-canvas {
  display: flex;
  position: relative;
  /* flex: 1 1 40%; */
  max-width: 35vw;
  max-height: 100%;
  box-shadow: 0px 0px 15px 2px rgba(0, 255, 55, 0.603);
}

/* EXPIREMENTS CSS */

.buttons {
  display: flex;
  gap: 2rem;
  margin: 5rem;
  justify-content: center;
}

.exp-button {
  color: rgb(18, 211, 1);
  font-size: large;
  border-radius: .5rem;
  padding: .5rem;
  box-shadow: 0px 0px 15px 2px rgba(0, 255, 55, 0.603);
  background-color: rgba(255, 255, 255, 0.178);
  backdrop-filter: blur(8px);
}

.expirement-wrapper {
  margin: auto;
}

.expirement {
  display: flex;
  height: 50vh;
  justify-content: center;
}

/* RESUME CSS */

.page {
  color: black;
  text-align: start;
  margin: 3rem;
  padding: 1.5rem;
  background-color: rgba(240, 231, 182, 0.589);
  backdrop-filter: blur(8px);
}

.page h2 {
  text-align: center;
}

.page p {
  font-size: medium;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.5);
  line-height: 1rem;
  margin: .5rem;
}

.page h4 p {
  display: inline;
  font-size: 1.5ch;
  font-weight: 400;
}

.info-date{
  display: grid;
  width: 100%;
  grid-template-columns: 3fr .5fr;
  grid-row: 1;
}

.info-date div{
  text-align: end;
}

.page li {
  max-width: 80ch;
}
